import React, { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import { Button } from 'antd';
import AuthInput from '@components/common/authInput/AuthInput';
import { EAuthInputTypes, IAuthData } from '@models/auth';
import { useAppDispatch } from '@store/index';
import { login } from '@store/redusers/auth';
import cl from './AuthForm.module.scss';

const AuthForm: React.FC = () => {
  const navigate = useNavigate();
  const dispath = useAppDispatch();

  const submitSuccess = useCallback((values: IAuthData) => {
    dispath(login({ ...values }));
    navigate('hotels');
  }, []);

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
      }}
      validationSchema={Yup.object({
        email: Yup.string()
          .email(' email ')
          .required(' !'),
        password: Yup.string()
          .required(' !')
          .matches(/[a-z0-9]{8,}/gi, {
            message: ' 8   ',
          }),
      })}
      validateOnChange={false}
      validateOnBlur={false}
      onSubmit={(values, { setSubmitting }) => {
        setTimeout(() => {
          submitSuccess(values);
          setSubmitting(false);
        }, 400);
      }}
    >
      {({ isSubmitting }) => (
        <Form className={cl.form}>
          <h1 className={cl.title}>Simple Hotel Check</h1>
          {/*  type=text           */}
          <AuthInput label="" name={EAuthInputTypes.EMAIL} type="text" />
          <AuthInput
            label=""
            name={EAuthInputTypes.PASSWORD}
            type={EAuthInputTypes.PASSWORD}
          />
          <Button
            className={cl.button}
            htmlType="submit"
            block
            disabled={isSubmitting}
          >
            
          </Button>
        </Form>
      )}
    </Formik>
  );
};

export default AuthForm;
